@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Lato');

.Professionalism-Body {
    background-image:url(../../public/Images/Pillars/Professionalism/workspace.jpg);
    background-attachment: fixed;
    background-size: cover;
    min-height: 60vh;
    background-position: center;
}

.mainContainer5 {
    background-color: #222222;
    height: 100%;
}

.Professionalism-Body > h1 {
    margin: 0;
    padding: 40px;
    color: white;
    padding-top: 199px;
    padding-bottom: 199px;
    text-transform: uppercase;
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    font-size: 80px;
}

.container5 p {
    color: white;
    text-align: center;
    font-weight: 400;
    font-family: "Lato", sans-serif;
    font-size: 20px;
    line-height: 1.5;
    word-spacing: 0.01em;
    margin-top: 10px;
    
}

.container5 {
    max-width: 1000px;
    margin: auto;
    padding-top: 100px;
    padding-bottom: 50px;
    
    
}

.paragraph5 > h2 {
    font-weight: 200;
    font-size: 50px;
    margin: -20px 0 0 0;
}

.paragraph5 {
    margin: 10px 0 60px 0;
    color: white;
    text-align: center;
    font-weight: 400;
    font-family: "Lato", sans-serif;
    font-size: 20px;
    line-height: 1.5;
    word-spacing: 0.01em;
    text-transform: none !important;
    letter-spacing: 0px !important;
}

.professionalismImages {
    width: auto;
    max-width: 100%;
    border-width: 0;
    margin: 0;
    padding: 0;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    display: block;
}

.textUnderImage {
    display: block;
    font-size: 80%;
    text-align: center;
    color: #bbbbbb;
    font: "Lato", sans-serif;
    line-height: 1.25;
    padding-top: 5px;
    font-weight: 500;
}
.hyperLink {
    color: white;
}

.shpeLogo {
    width: 413px;
    display: inline-block;
    max-width: 100%;
}

.discordTextBox {
    position: relative;
    margin-bottom: 30px;
    padding-left: 30px;
}

.discordServerLetters {
    color: white;
    font-family: "Lato", sans-serif;
    font-size: 32px;
    font-style: normal;
}

.box {
    display: inline-block;
    width: 45%;
    position: absolute;
    margin: 120px 0 0 30px;
}

.horizontalLine5 {
    height: 0.5px;
    color: rgba(156, 156, 156, 0.13);
    background-color: rgba(156, 156, 156, 0.13);
    margin: 30px 0 30px 0;
}

.smallTitles2 {
    color: white;
    font-family: "Lato", sans-serif;
    font-size: 32px;
    text-align: center;
    font-weight: lighter;
    margin: 20px 0 20px 0;
}

.campusResources {
    color: #d5d5d5;
    text-decoration: none;
    border-bottom: 1px solid #333333;
    font-family: "Lato", sans-serif;
    font-size: 20px;
    font-weight: 400;
    word-spacing: 0.01em;
    line-height: 1.5;
    word-wrap: break-word;
    text-transform: none;
    letter-spacing: 0px;
}

.classResourcesDiv {
    text-align: center;
}

.onlineResources {
    font-family: "Lato";
    word-spacing: 0.01em;
    line-height: 1.5;
}

.list li{
    list-style-type: disc;
}

.list {
    color: #d5d5d5;
    font-size: 20px;
}

.list ul > li {
    padding-left: 5px;
    margin: 3px 0 0;
    color: #fff;
    font-weight: 400;
    text-transform: none;
    word-wrap: break-word;
    text-align: left;
}

.list a {
    color: #d5d5d5;
    border-bottom: 1px solid #333333;
    text-decoration: none;
    transition: all 240ms ease-in-out;
    font-weight: 700;
    font-size: 20px;
    text-align: left;
    word-wrap: break-word;
    
}

.list > li {
    padding-left: 5px;
    margin: 3px 0 0;
}

.end {
    margin: 60px 0 60px 0;
}



